.register-form {
  max-width: 450px;
  margin: 0px auto;
  padding: 0px 20px;
  position: relative;
}

.register-form img {
  max-width: 250px;
  margin: 0px auto;
  display: block;
}

.head-logo img {
  max-width: 100px;
  margin: 0px auto;
  display: block;
  border: 2px solid #ff5917;
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 20px;
}

.register-form h1 {
  font-size: 30px;
  font-weight: 600;
  text-align: center;
  max-width: 400px;
  margin: 0px auto;
  color: #ddd;
  margin-bottom: 0.5rem;
}

.register-form p {
  text-align: center;
  color: #a9a9a9;
  font-size: 14px;
  max-width: 270px;
  margin: 0px auto;
}

.register-form h1 span {
  color: #f5d35c;
}

.register-form .rigister-tab {
  margin-top: 20px;
  margin-bottom: 0px;
}

.terms-link {
  color: #a9a9a9;
  text-decoration: underline;
}

.terms-link:hover {
  color: #f5d35c;
}

.dis-show .form-check {
  display: inline-block;
}

.dis-show p {
  font-size: 13px;
  color: #a9a9a9;
  margin: 0;
  text-align: left;
}

.dis-show {
  display: flex;
  align-items: flex-start;
}

.s-btn.btn.btn-primary {
  padding: 8px 40px;
  font-size: 15px;
  background: #a9f55c;
  border-radius: 3px;
  font-weight: 600;
  color: #1b1f22;
  border: none;
  transition: all 0.5s;
  width: 100%;
  margin-top: 15px;
}

.s-btn.btn.btn-primary:hover,
.s-btn.btn.btn-primary:focus,
.s-btn.btn.btn-primary:active {
  background: #a9f55c;
  box-shadow: 0 0 50px 0 hsl(0deg 0% 100% / 30%);
  border: none;
}

.register-form .form-group .form-control {
  height: auto;
  padding: 8px 15px;
  background: #192633;
  border: 1px solid #192633;
  border-radius: 4px;
  font-size: 14px;
  color: #a9a9a9;
}

.register-form .form-group .form-control::placeholder {
  color: #a9a9a9;
}

.register-form .form-group .form-control:focus {
  box-shadow: none;
  background: #192633;
}

.register-form input::-internal-autofill-selected {
  background: transparent !important;
}

.form-control:-webkit-autofill,
.form-control:-webkit-autofill:hover,
.form-control:-webkit-autofill:focus {
  border: 1px solid #192633;
  -webkit-text-fill-color: #ffffff;
  -webkit-box-shadow: 0 0 0px 1000px #192633 inset;
  transition: background-color 5000s ease-in-out 0s;
  outline: none;
}

.signup .form-label {
  font-size: 14px;
  color: #ddd;
  font-weight: 500;
  margin-bottom: 5px;
}

.already {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 10px;
  font-size: 13px;
  color: #a9a9a9;
}

.signup {
  position: relative;
}

input[type="date"].form-control,
input[type="datetime-local"].form-control,
input[type="month"].form-control,
input[type="time"].form-control {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: relative;
}

input[type="date"]:before {
  background: none;
  display: block;
  font-family: "FontAwesome";
  content: "\f073";
  width: 15px;
  height: 20px;
  position: absolute;
  top: 8px;
  right: 15px;
  color: #a9a9a9;
  cursor: pointer;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.signup-popup .modal-header {
  border-bottom: none;
  padding-bottom: 0;
}

.signup-popup .modal-header .close:focus {
  outline: none;
}

.signup-popup .close {
  color: #a9a9a9;
  text-shadow: none;
  font-weight: 400;
  font-size: 30px;
  cursor: pointer;
}

.signup-popup .close:hover {
  color: #efebeb;
}

.already {
  text-align: center;
  padding-top: 30px;
  font-size: 13px;
  color: #a9a9a9;
}

.signup-popup .modal-content {
  position: relative;
  background-size: cover;
  background-position: center;
  position: relative;
  background: #1f2127;
}

.signup-popup .modal-body {
  padding-top: 0;
}
