/* reset css start */

html {
  scroll-behavior: smooth;
}

body {
  padding: 0;
  margin: 0;
  font-weight: 400;
  position: relative;
  background-color: #0f0232;
}

img {
  max-width: 100%;
  height: auto;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

button {
  cursor: pointer;
}

*:focus {
  outline: none;
}

button {
  border: none;
}

button:focus {
  outline: none;
}

input:not([type="radio"]),
textarea {
  padding: 10px 20px;
  border: 1px solid #6472d9;
  width: 100%;
  background-color: transparent;
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  color: #e9e9e9;
}

input:not([type="radio"])::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #9aace5;
}

input:not([type="radio"])::-moz-placeholder,
textarea::-moz-placeholder {
  color: #9aace5;
}

input:not([type="radio"]):-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #9aace5;
}

input:not([type="radio"]):-moz-placeholder,
textarea:-moz-placeholder {
  color: #9aace5;
}

select {
  padding: 10px 20px;
  width: 100%;
  border: 1px solid rgba(255, 255, 255, 0.1);
  cursor: pointer;
  color: #e9e9e9;
  background-color: transparent;
  height: 50px;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}

textarea {
  min-height: 150px;
  resize: none;
  width: 100%;
}

span {
  display: inline-block;
}

a:hover {
  color: #ffb200;
}

.text-danger {
  color: #ffb200 !important;
}

.site-logo img {
  width: 160px;
}
/* reset css end */
