.curve-shape::after {
  position: absolute;
  content: "";
  top: -85px;
  left: 0;
  width: 100%;
  height: 150px;
  background-color: #0f0232;
  -webkit-clip-path: ellipse(55% 75% at 50% 75%);
  clip-path: ellipse(55% 75% at 50% 75%);
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
  z-index: -1;
}

