  /*background-image: -ms-linear-gradient(7deg, #ec1379 0%, #6c0092 100%);*/
}

.theme-switcher__body .single.active {
  position: relative;
}

.theme-switcher__body .single.active::after {
  position: absolute;
  top: -8px;
  right: -6px;
  width: 25px;
  height: 25px;
  background-color: #5039fb;
